export const pluginAction = {
    initialPluginData: (action) => {
        return {
            type: 'INIT_PLUGIN_DATA',
            ...action
        };
    },
    installPlugin: (action) => {
        const { slug, name, path, version } = action;
        return {
            type: 'INSTALL_PLUGIN',
            slug,
            name,
            path,
            version
        };
    },
    updatePlugin: (slug, version) => {
        return {
            type: 'UPDATE_PLUGIN',
            slug,
            version
        };
    },
    activatePlugin: (slug) => {
        return {
            type: 'ACTIVATE_PLUGIN',
            slug
        };
    },
};

export const demoReducer = (state = {}, action) => {
    switch (action.type) {
        case 'INIT_DATA':
            const {
                demoData,
            } = action;

            return {
                ...state,
                demoData,
            };
        case 'DEMO_LIKE':
            return {
                ...state,
                demoData: state.demoData.map(demo => {
                    if (action.slug === demo.data.slug) {
                        return {
                            ...demo,
                            like: action.flag
                        };
                    }

                    return demo;
                })
            };
        case 'DEMO_IMPORT':
            return {
                ...state,
                demoData: state.demoData.map(demo => {
                    if (action.slug === demo.data.slug) {
                        return {
                            ...demo,
                            imported: action.flag
                        };
                    }

                    return demo;
                })
            };
        case 'DEMO_CATEGORIES':
            const {
                demoCategoryData,
            } = action;
            return {
                ...state,
                demoCategoryData: demoCategoryData
            };
        default:
            return state;
    }
};

export const pluginReducer = (state = {}, action) => {
    switch (action.type) {
        case 'INIT_PLUGIN_DATA':
            const {
                installedPlugin
            } = action;

            return {
                installedPlugin
            };
        case 'INSTALL_PLUGIN':
            return {
                installedPlugin: {
                    ...state.installedPlugin,
                    [action.slug]: {
                        name: action.name,
                        version: action.version,
                        path: action.path,
                        active: true,
                    },
                }
            };
        case 'UPDATE_PLUGIN':
            return {
                installedPlugin: {
                    ...state.installedPlugin,
                    [action.slug]: {
                        ...state.installedPlugin[action.slug],
                        active: true,
                        version: action.version,
                    }
                },
            };
        case 'ACTIVATE_PLUGIN':
            return {
                installedPlugin: {
                    ...state.installedPlugin,
                    [action.slug]: {
                        ...state.installedPlugin[action.slug],
                        active: true,
                    }
                },
            };
        default:
            return state;
    }
};

export const controlReducer = (state = {}, action) => {
    switch (action.type) {
        case 'INIT_CONTROL_DATA':
            const {
                controlData
            } = action;
            return {
                controlData
            };
        case 'SET_CONTROL_DATA':
            window.essential_setting_change = action.action.option;
            return {
                ...state,
                controlData: {
                    ...state.controlData,
                    [action.action.option]: action.action.value
                },
            };
        default:
            return state;
    }
};

