export const dashboardReducer = (state = {}, action) => {
    switch (action.type) {
        case 'INIT_MODAL_DATA':
            const {
                demoContentData
            } = action;

            return {
                demoContentData
            };
        case 'SET_KEYWORD':
            return {
                ...state,
                demoContentData: {
                    ...state.demoContentData,
                    keyword: action.keyword
                }
            };
        case 'SET_PAGING':
            return {
                ...state,
                demoContentData: {
                    ...state.demoContentData,
                    paging: action.paging
                }
            };
        case 'SET_LIKE':
            return {
                ...state,
                demoContentData: {
                    ...state.demoContentData,
                    like: action.like
                }
            };
        case 'SET_IMPORTED':
            return {
                ...state,
                demoContentData: {
                    ...state.demoContentData,
                    imported: action.imported
                }
            };
        case 'SET_DEMO_IMPORT':
            return {
                ...state,
                lockDemoImport: action.demo
            };
        case 'SET_DEMO_PROGRESS':
            return {
                ...state,
                demoProgress: action.text
            };
        default:
            return state;
    }
};

