export const templateAction = {
    initialTemplate: (action) => {
        return {
            type: 'INIT_TEMPLATE_DATA',
            ...action
        };
    },
    setTemplate: (action) => {
        return {
            type: 'SET_TEMPLATE_DATA',
            action
        };
    },
    addTemplate(template, key) {
        return {
            type: 'ADD_TEMPLATE',
            template,
            key
        };
    },
};


export const templateSelector = {
    getTemplateData: (state) => {
        return state.template;
    },
};

export const templateReducer = (state = {}, action) => {
    switch (action.type) {
        case 'INIT_TEMPLATE_DATA':
            const {
                template
            } = action;
            return {
                ...template
            };
        case 'SET_TEMPLATE_DATA':
            return {
                ...state,
                [action.action.key]: action.action.value
            };
        case 'ADD_TEMPLATE':
            return {
                ...state,
                temlateData: {
                    ...state.temlateData,
                    [action.key]: [...state.temlateData[action.key], action.data]
                }
            };
        default:
            return state;
    }
};